.Logo {
  border-radius: 50%;
  width: 7%;
  height: 100%;
}
.Logo > img {
  width: 11vh;
  margin-top: 0;

  border: 3px solid black;
}
.header {
  display: flex;
  width: 100%;
}
.user_photo {
  align-self: flex-end;
}
.main_data {
  width: 80%;
}
.main_data {
  display: flex;
  justify-content: center;

  text-align: center;
  padding-left: 20px;
}

.he {
  font-family: "Shadows Into Light", cursive;
  font-size: 2.2rem;
  padding-top: 10px;
}




@media (max-width: 1000px) {
    .toggle-button {
    display: block;
}
  .bgImage .sectionContainer .rightSection {
    width: 77%;
    height: 86vh;
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: rgba(246, 224, 108, 0.66);
    border-radius: 20px;
    margin: 1.1rem;
    justify-content: flex-start;
  }

  .bgImage .sectionContainer .leftSection {
        width: 40%;
    height: 90vh;
    background-color: rgba(127, 218, 218, 0.676);
    z-index: 2;
    position: absolute;
    left: 0;
  }
}

@media (max-width: 600px) {
    .toggle-button {
    display: block;
}
  .bgImage .sectionContainer .rightSection {
    width: 77%;
    height: 86vh;
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: rgba(246, 224, 108, 0.66);
    border-radius: 20px;
    margin: 1.1rem;
    justify-content: flex-start;
  }

  .bgImage .sectionContainer .leftSection {
        width: 50%;
    height: 90vh;
    background-color: rgba(127, 218, 218, 0.676);
    z-index: 2;
    position: absolute;
    left: 0;
  }
}
